.projectBlock{
  width: 300px;
  height: 300px;
  background: #9c9c9c;
}


* {
  box-sizing: border-box;
}

html,
body,
#root {
  //width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
}

.content-image {
  width: 25%;
  height: 75%;
  margin: 0;
  padding: 0;
  box-shadow: 0px 10px 30px -5px rgba(0, 0, 0, 0.3);
  transition: box-shadow 0.5s;
  will-change: transform;
  border-radius: 5px;
}

.content-image:hover {
  box-shadow: 0px 30px 100px -10px rgba(0, 0, 0, 0.4);
}

.content {
  width: 100%;
  height: 20vh;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
}
