.arrowDown{
  cursor: pointer;
  margin-top: 19%;
  animation-name: rot;  /* задаём имя анимации (соответствует имени назначенному в ключевых кадрах)  */
  animation-duration: 8s;  /* задаём продолжительность анимации */
  animation-iteration-count: infinite;
  animation-delay: 0s;
}
@keyframes rot {
  0%   {opacity: 1;}
  25%  {opacity: 0.2}
  50%  {opacity: 1}
  75%  {opacity: 0.2}
  100% {opacity: 1}
}