@font-face {
  font-family: 'Source Sans Pro';
  font-weight: 400;
  src: local('Open Sans'), url(./fonts/SourceSansPro-Regular.ttf) format('truetype');
}
@font-face {
  font-family: 'Source Sans Pro';
  font-weight: 700;
  src: local('Source Sans Pro'), url(./fonts/SourceSansPro-Bold.ttf) format('truetype');
}
.App{
  margin-left: 150px;
  margin-right: 150px;
  margin-top: 50px;
  font-family: Source Sans Pro;
}
#root{
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
body{
  //background: #131313;
  margin: 0%;
  //overflow: hidden;
  background: rgb(17, 17, 19);
}
.bganime{
  position: fixed;
  z-index: -1;
}
.menu{
  //position: absolute;
  //top:50px;
  //right: 600px;
  display: flex;
  justify-content: space-between;
  width: 790px;
  height: max-content;
}
.menuWord{
  font-weight: bold;
  font-size: 36px;
  color: #E0E0EC;
}
.menuWrapper{
  //margin-top: 40px;
  //margin-right: 100px;
  //margin-left: 1000px;
}
.headerWrapper{
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 100;
  height: 80px;
  background: transparent;
  //position: fixed;
  //top:0;
  width: 100%;
}
.footer{
  text-align: center;
  color: #737379;
  margin-bottom: 30px;
}
.footerText{
  color: white;
  font-family: Open Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 44px;
  line-height: 55px;
  width: 630px;
  height: max-content;
}
.footerWrapper{
  padding-top: 4%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-left: 300px;
  margin-right: 300px;
}
.footerMail{
  width: 630px;
  height: 200px;
}
.active{
  color: #64FFFF;
  transition: color ease-in-out .3s;
}
a{
  color: black;
  text-decoration: none;
  font-size: 16px;
}
.arrowTop{
  cursor: pointer;
  position: fixed;
  right: 60px;
  bottom: 100px;
  opacity: 0;
  transition: opacity ease-in-out .3s;
}
.contact-form{
  display: flex;
  flex-direction: column;
}
.topBarEmail{
  width: 85.6%;
  display: flex;
  justify-content: space-between;
}
.blockEmail{
  display: flex;
  flex-direction: column;
}
label{
  font-size: 14px;
  color: #a5a5a5;
  padding-bottom: 7px;
}
.inputEmail{
  margin-bottom: 30px;
  width: 130%;
  height: 34px;
  border-style: solid;
  border-width: 2px;
  border-color: hsla(0, 0%, 100%, 0.02);
  border-radius: 0px;
  background-color: #161616;
  font-family: "Open Sans";
  line-height: 1.42857143;
  padding-left: 12px;
  padding-right: 12px;
  padding-bottom: 8px;
  padding-top: 8px;
  color: #fff;
  outline: none;
  font-size: 16px;
  font-weight: 400;
}
::placeholder{
  color: hsla(0, 0%, 100%, 0.25);;
}
.message{
  padding-left: 12px;
  padding-right: 12px;
  padding-bottom: 8px;
  padding-top: 8px;
  margin-bottom: 30px;
  height: 80px;
  font-family: "Open Sans";
  border-style: solid;
  border-width: 2px;
  border-color: hsla(0, 0%, 100%, 0.02);
  background-color: #161616;
  line-height: 1.42857143;
  color: #fff;
  outline: none;
  font-size: 16px;
  font-weight: 400;
  resize: none;
}
.sendEmail{
  padding-left: 22px;
  padding-right: 22px;
  font-family: "Open Sans";
  font-weight: bold;
  font-size: 16px;
  text-align: center;
  vertical-align: center;
  color: black;
  background: white;
  border-radius: 30px;
  width: max-content;
  height: 44px;
  cursor: pointer;
  outline: none;
  border: transparent;
}
.activeSend{
  display: none;
}
.afterSend{
  display: none;
  position: absolute;
  top:0;
  border-style: solid;
  border-width: 2px;
  border-color: hsla(0, 0%, 100%, 0.02);
  background-color: #161616;
  height: 310px;
  width: 100%;
  color: white;
  font-weight: normal;
  font-size: 16px;
  text-align: center;
}
.emailWrapper{
  position: relative;
}
.afterSendText{
  margin-top: 8%;
}
.chek{
  margin-top: 8%;
}
.topStText{
  margin-bottom: 3px;
}
.botFooter{
  align-items: center;
  width: 68%;
  display: flex;
  justify-content: space-between;
  color: grey;
  margin-top: 200px;
  margin-left: 300px;
  margin-right: 300px;
}
.icons{
  //width: max-content;
  height: max-content;
  display: flex;
  align-items: flex-end;
}
.iconTelegram{
  fill:grey;
  transition: fill ease-in-out .25s;
}
.telegram{
  padding-left: 25px;
}
.telegram:hover{
  cursor: pointer;
  .iconTelegram{
    fill:white;
  }
}
h1{
  color: #E0E0EC;
}
.textWrapper{
  margin-top: 20%;
  margin-bottom: 100px;
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.imgPhoto{
  width: 567px;
  height: 754px;
}
.nameSection{
  font-weight: bold;
  font-size: 48px;
  margin-top: 35px;
  margin-bottom: 35px;
  color: #E0E0EC
}
.textParagraph{
  margin-bottom: 25px;
  color: #C6C6D0;
  width: 100%;
  font-size: 36px;
  font-weight: normal;
}
.textBox{
  width: 52.8%;
  //margin-right: 0;
  //padding-right: 0;
  text-align: left;
}
.contacts{
  margin-top: 10%;
}
.fl{
  display: flex;
  align-items: center;
  width:max-content ;
}
.space{
  display: flex;
  justify-content: center;
}
.imgIcon{
  margin-right: 8px;
}
.contacts{
  margin-bottom: 17%;
}
